<template>
	<div class="body">
		<div class="cash_flow_table_box">
			<div class="cash_flow_table_title flex_between">
				<div class="flex_start">
					<span class="circle_blue"></span>
					<span class="name">投资</span>
				</div>
			</div>
			<div class="cash_flow_table table">
				<el-table ref="multipleTable" :data="datalist" border style="width: 100%" empty-text="">
					<el-table-column prop="unit" label="币种">
						<template slot-scope="scope">
							{{ scope.row.unit == 1?'人民币':'美元' }}
						</template>
					</el-table-column>
					<el-table-column prop="event" label="事件" width="250">
					</el-table-column>
					<el-table-column prop="fund_type_name" label="投资基金类别">
					</el-table-column>
					<el-table-column prop="fund_name" label="基金名称">
					</el-table-column>
					<el-table-column prop="invest_date" label="出资日期">
					</el-table-column>
					<el-table-column prop="fee" label="金额（万元）">
					</el-table-column>
					<el-table-column prop="should_fee" label="应计金额（万元）">
					</el-table-column>
				</el-table>
			</div>
		</div>
	</div>
</template>

<script>
	import { getDataCompanyRunCashInvestApi } from '../../config/api'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	
	export default {
		data() {
			return {
				id: '',
				count: '',
				type: '',
				datalist: [],
				coinList: [
					{
						label: '人民币',
						value: 1
					},
					{
						label: '美元',
						value: 2
					}
				]
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.count = this.$route.query.count;
			this.type = this.$route.query.type;
			this.getInitData()
		},
		computed: {
	
		},
		mounted() {},
		methods: {
			getInitData(){
				getDataCompanyRunCashInvestApi({
					sg_company_manager_id: this.id,
					count: this.count,
					type: this.type,
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.$set(this.datalist,0,res.data.payload.data);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import '../../style/cashFlow.scss'; 
</style>


